<template>
  <div class="wrapper" :class="{ 'nav-open': $sidebar.showSidebar }">
    <div class="dashboard-main-panel">
      <top-navbar></top-navbar>
      <div class="container existing" style="width: 100%;max-width: 100%;">
        <main class="content js-content">
          <div class="md-content">
            <div class="md-layout">
              <div
                class="md-layout-item md-large-size-100 md-medium-size-100 md-xsmall-size-100 md-size-33"
              >
                <div class="panel-body">
                  <div class="demo mb-5">
                    <div class="demo-wrapper">
                      <div class="form-group has-search">
                        <label for="search" id="searchico">
                          <i aria-hidden="true" class="fa fa-search"></i>
                          <span class="sr-only">Search icons</span>
                        </label>
                        <input type="text" class="form-control" style="padding-left: 24px;" v-on:keyup="onSearch($event);" placeholder="Snippet Search">
                      </div>
                    </div>
                  </div>
                  <div class="demo">
                    <div class="demo-wrapper snippet-wrap">
                      <div v-for="(item, index) in items" v-bind:index="index" v-bind:key="item._id" class="reviews-members pb-4">
                        <div v-if="user._id == item._source.useridPadre">
                          <div class="media">
                            <div class="col-lg-1 col-md-1 col-sm-1">
                              <a href="#">
                                <img width="50" alt="Generic placeholder image" :src="item._source.imageAccount" class="mr-3 rounded-pill">
                              </a>
                            </div>
                            <div class="col-lg-8 col-md-8 col-sm-8">
                              <div class="media-body" style="margin-left: 10px;">
                                <div class="reviews-members-header">
                                  <h6 class="mb-1">
                                    <!-- <img src="../assets/img/open.png" style="width: 0.8em;margin-left: -23px;margin-right: 10px;"/> -->
                                    <a class="text-black" href="#">{{item._source.title}}</a>
                                  </h6>
                                </div>
                                <div class="reviews-members-body">
                                  <p class="text-black">
                                    {{item._source.content | strippedContent}}
                                  </p>
                                </div>
                              </div>
                            </div>
                            <div class="col-lg-3 col-md-3 col-sm-3 mt-2">
                              
                              <button class="demo-button" v-on:click="saveFiles(item, item._id)" id="save" :disabled='item.isDisabled'>
                                <div class="save-btn">Save</div>
                                <md-progress-spinner v-if="item.loading" :md-diameter="20" :md-stroke="3" md-mode="indeterminate"></md-progress-spinner>
                              </button>
                            </div>
                          </div>
                        </div>  
                      </div>
                    </div>
                  </div>
                  <div class="demo mt-5">
                    <div class="demo-wrapper text-center">
                      <router-link :to="'/new-snippet'" class="link" data-section="new-snippet">If you have not found a snippet, create a new one.</router-link>
                    </div>
                  </div>
                  <md-snackbar v-bind:class="[hasError ? 'danger' : 'success']" :md-position="'center'" :md-active.sync="showSnackbar" md-persistent>
                      <span>{{message}}</span>
                  </md-snackbar>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../assets/bootstrap/css/bootstrap.min.css";
  @import "../assets/css/variables.css";
  @import "../assets/css/global.css";
  @import "../assets/css/nav.css";
  @import "../assets/css/demo.css";

  ::v-deep .md-progress-spinner-circle {
    stroke: #FFF !important;
  }

  ::v-deep .md-progress-spinner.md-theme-default .md-progress-spinner-circle{
    stroke: #FFF !important;
  }

  .md-progress-spinner, 
  .md-progress-spinner-indeterminate, 
  .md-indeterminate{
    float: left;
    margin-left: 15px;
    margin-top: 1px;
  }

  .save-btn {
    float:left;
  }
</style>
<script>
  import TopNavbar from "./Layout/TopNavbar.vue";
  import { mapActions, mapGetters } from "vuex";
  import axios from "axios";
  let baseUrl = "http://localhost:7000";
  export default {
    components: {TopNavbar},
    data() {
      return {
        sidebarBackground: "dodger-blue",
        sidebarBackgroundImage: require("@/assets/img/sidebar-2.jpg"),
        showSnackbar: false,
        items : [],
        message : "",
        hasError: false
      }
    },
    computed: mapGetters(["user","files"]),
    methods: {
      ...mapActions(["getProfile","getFiles"]),
      onSearch(event) {
        let snippet = {
          q : event.target.value,
          user : this.user
        };
        axios
          .post(`${baseUrl}/api/snippet/search`, snippet)
            .then(( res )=>{
              this.items = res.data;
            })
            .catch(err => {
              //console.log( err );
            });
      },
      saveFiles( item, id ) {
        let uploadObj = {
          files : this.files,
          user : this.user
        }

        if( this.user._id == item._source.useridPadre ){
          if( this.files.length > 0 ){
            item.loading = true;
            axios
              .post( `${baseUrl}/api/files/upload/${id}`, uploadObj )
                .then(( response )=>{
                    item.loading = false;
                    this.hasError = false;
                    this.showSnackbar = true;
                    this.message = `File saved for this snippet ID ${id}`;
                })
                .catch(err => {
                  item.loading = false;
                  this.hasError = true;
                  this.showSnackbar = true;
                  this.message = `Error while saving saving file`;
                });
          } else {
            this.hasError = true;
            this.showSnackbar = true;
            this.message = `There are no files to save!!!`;
          }
        } else {
          this.hasError = true;
          this.showSnackbar = true;
          this.message = `File can not be saved for this user`;
        }
      }
    },
    filters: {
      strippedContent: function(string) {
          var fullString = string.replace(/<\/?[^>]+>/ig, " ");
          var dot = fullString.length > 100 ? "..." : "";
          return fullString.substring(0,100)+dot; 
      }
    },
    mounted () {
      
    },
    created() {
      this.getProfile();
      this.getFiles();
    }
  };
</script>